import React from 'react';
import { Link } from 'react-router-dom';

function ProfileLayout({ userDetails }) {
    return (
        <div>
            <div className = "card" id = "card-mobile">
                <div>
                    <div>
                        <img src={userDetails.avatar_url} alt="Avatar" className="img"></img>
                    </div>
                    <div>
                        <a href={userDetails.html_url} target="_blank" class = "visit-link">Visit Profile on Github</a>
                        <div className = "user-info-block">
                            <div className = "user-info-sub-block">
                                <Link to={"/Followers/" + userDetails.login}>
                                    <p className = "user-info-stat"> Followers </p>
                                    <p className = "user-info-stat-value">{userDetails.followers}</p>
                                </Link>
                            </div>
                            <div className = "user-info-sub-block">
                                <Link to={"/Following/" + userDetails.login}>
                                    <p className = "user-info-stat" > Following </p>
                                    <p className = "user-info-stat-value">{userDetails.following}</p>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <div className="container">
                        <h2><b>{userDetails.name}</b> ({userDetails.login})</h2>
                        {userDetails.company && <h3>Works at <b>{userDetails.company}</b></h3>}
                        {userDetails.location && <h3>Currently in <b>{userDetails.location}</b></h3>}
                        <p>{userDetails.bio}</p>
                        <Link className="user-info__text" to={"/Repositories/" + userDetails.login}>
                            <p className="user-info-stat" > Repositories: {userDetails.public_repos}</p>          
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProfileLayout;


