import React from 'react';

function FollowingLayout({ followingData }) {
    return (
        <div>
            <div>
                <center>
                    <h2 className="heading">FOLLOWING</h2>
                </center>
                <div className = "collection">
                {followingData.map((following) => (
                    <div className="card" id = "profiles">
                        <img src={following.avatar_url} alt="Avatar" className = "profile-img" />
                        <div className="container">
                            <h4><b>{following.login}</b></h4>
                            <a href={following.html_url} target="_blank">Visit Profile on Github</a>
                        </div>
                    </div>
                ))}
                </div>
            </div>
        </div>
    )
}

export default FollowingLayout;


